import React from "react";
import moviePhoto from "../image/movie.jpg";
import weatherPhoto from "../image/weather.jpg";

const Work = () => {
  return (
    <div className="container" style={{ marginTop: "2%" }}>
      <h1 style={{ textAlign: "center" }}>My Projects</h1>
      <div className="row" style={{ marginTop: "4%" }}>
        <div className="col-sm-3">
          <div className="card  mb-3 shadow-sm">
            <img src={moviePhoto} className="card-img-top" />
            <div className="card-body">
              <h5 className="card-title">Movie App</h5>
              <p className="card-text">
                This is a movie app which uses TMDB API.
              </p>
              <a href="/movie" className="btn btn-dark">
                Go to App
              </a>
            </div>
          </div>
        </div>
        <div className="col-sm-3">
          <div className="card  mb-3 shadow-sm">
            <img src={weatherPhoto} className="card-img-top" />
            <div className="card-body">
              <h5 className="card-title">Weather App</h5>
              <p className="card-text">
                This is a weather app which uses OpenWeatherMap API.
              </p>
              <a href="/weather" className="btn btn-dark">
                Go to App
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Work;
