import React from "react";
import contactLogo from "../image/contact.webp";

const Contact = () => {
  return (
    <div className="container">
      <div className="row">
        <div
          className="col-xs-12"
          style={{
            textAlign: "center",
          }}
        >
          <img
            src={contactLogo}
            style={{
              width: "20%",
              height: "auto",
              marginBottom: "1%",
              marginTop: "1%",
            }}
          />
        </div>
        <h2 style={{ textAlign: "center" }}>
          <i>Let's Talk</i>
        </h2>
        <div className="col-xs-12" style={{ textAlign: "center" }}>
          <a href="https://www.linkedin.com/in/mmelihatalay/" target="_blank">
            <i
              class="fa fa-linkedin-square"
              style={{ color: "black", fontSize: "48px" }}
            >
              &nbsp;
            </i>
          </a>
          <a href="https://github.com/mmelihatalay/" target="_blank">
            <i
              class="fab fa-github"
              style={{ color: "black", fontSize: "48px" }}
            >
              &nbsp;
            </i>
          </a>
          <a href="https://www.instagram.com/mmelihatalay/" target="_blank">
            <i
              class="fa fa-instagram"
              style={{ color: "black", fontSize: "48px" }}
            >
              &nbsp;
            </i>
          </a>
          <a
            href="https://www.researchgate.net/profile/Mehmet-Atalay-7"
            target="_blank"
          >
            <i
              class="fab fa-researchgate"
              style={{ color: "black", fontSize: "48px" }}
            >
              &nbsp;
            </i>
          </a>
          <a href="mailto:atalay.mehmetmelih@gmail.com" target="_blank">
            <i
              class="material-icons"
              style={{ color: "black", fontSize: "48px" }}
            >
              email
            </i>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Contact;
